.Navbar {
    color: rgb(13, 26, 38);
    height: 60px;
    line-height: 60px;
    display: flex;
    justify-content: space-between;
    padding: 0 80px;
}

.logo {
    font-size: 1.5em;
    height: 60px;
    letter-spacing: 1px;
    text-transform: uppercase;
}

.Navbar ul {
    display: inline-block;
    float: right;
    list-style: none;
    margin-top: -2px;
    text-align: right;
    transition: transform 0.5s ease-out;
    -webkit-transition: transform 0.5s ease-out;
}

@media (max-width: 640px) {
    .Navbar ul {
        display: none;
    }
}

@media (orientation: landscape) {
    .Navbar ul {
        display: inline-block;
    }
}

.Navbar li {
    display: inline-block;
}

.Navbar li a {
    color: rgb(13, 26, 38);
    display: block;
    font-size: 0.7em;
    height: 50px;
    letter-spacing: 1px;
    margin: 0 20px;
    padding: 0 4px;
    position: relative;
    text-decoration: none;
    text-transform: uppercase;
    transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
}

.Navbar a {
    color: rgb(13, 26, 38);
    display: block;
    font-size: 0.7em;
    font-weight: bold;
    height: 50px;
    letter-spacing: 1px;
    margin: 0 20px;
    padding: 0 4px;
    position: relative;
    text-decoration: none;
    text-transform: uppercase;
    transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
}

.Navbar li a:hover {
    color: rgb(28, 121, 184);
    transition: all 1s ease;
    -webkit-transition: all 1s ease;
}

/* Animated Bottom Line */
.Navbar li a:before, .Navbar li a:after {
    content: '';
    position: absolute;
    width: 0;
    height: 1px;
    bottom: -1px;
    background: rgb(13, 26, 38);
}

.Navbar li a:before {
    left: 0;
    transition: 0.5s;
}

.Navbar li a:after {
    background: rgb(13, 26, 38);
    right: 0;
}

.Navbar li a:hover:before {
    background: rgb(13, 26, 38);
    width: 100%;
    transition: width 0.5s cubic-bezier(0.22, 0.61, 0.36, 1);
}

.Navbar li a:hover:after {
    background: transparent;
    width: 100%;
}
