.contact-container {
    display: flex;
    flex-direction: column;
    margin: 50px auto;
    max-width: 500px;
    padding: 20px;
    color: rgb(13, 26, 38);
    border-radius: 3px;
    box-shadow: 0 2px 10px darkgrey;
}

form {
    display: flex;
    flex-direction: column;
}

label {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 20px;
    font-weight: bold;
}

input {
    margin-top: 10px;
    line-height: 30px;
    font-size: large;
    padding: 5px;
    width: 100%;
    box-sizing: border-box;
}

textarea {
    margin-top: 10px;
    width: 100%;
    height: 300px;
    font-size: large;
    padding: 5px;
    box-sizing: border-box;
}

button {
    width: 100px;
    height: 40px;
    font-size: large;
    font-weight: 600;
    border: 0;
    background-color: rgb(13, 26, 38);
    border-radius: 2px;
    color: white;
    align-self: center;
    cursor: pointer;
}
